import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			types: [{
					type: 1,
					title: '均摊',
					percent: 50.71,
					money: 221.00
				},
				{
					type: 1,
					title: '提成',
					percent: 30.1,
					money: 221.00
				}, {
					type: 1,
					title: '外部渲染',
					percent: 10.71,
					money: 221.00
				}, {
					type: 1,
					title: '其他',
					percent: 5.71,
					money: 221.00
				}
			],
			status: 0,
			timeStart: null,
			timeEnd: null,
			allOutMoney: 0,
			allInMoney: 0
		};
	},
	created() {
		this.timeEnd = this.$util.dateFormat('YYYY-mm-dd', new Date());
		this.timeStart = this.timeEnd.slice(0, 8) + '01';
		this.getTypeStatistic();
	},
	methods: {
		changeStatus(status) {
			this.status = status;
			this.sortTypes();
		},
		timeConfirm(time) {
			this.timeStart = time.range.data[0];
			this.timeEnd = time.range.data[time.range.data.length - 1];
			this.getTypeStatistic();
		},
		openCalendar() {
			this.$refs.calendar.open();
		},
		async getTypeStatistic() {
			let res = await this.$myRequest('/getTypeStatistic', {
				date: [this.timeStart, this.timeEnd]
			}, 'GET');
			if (res) {
				this.allInMoney = res.allInMoney;
				this.allOutMoney = res.allOutMoney;
				this.types = res.types;
				this.sortTypes();
			}
		},
		// 根据当前是支出还是收入，排序一下百分比
		sortTypes() {
			if (this.status) {
				this.types.sort((a, b) => {
					return b.inPercent - a.inPercent
				})
			} else {
				this.types.sort((a, b) => {
					return b.outPercent - a.outPercent
				})
			}
		}
	},
	components: {}
}
